* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #e0e0e0;
  background: #1a1a1a;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
  font-family: Ubuntu, sans-serif;
  display: flex;
}

.container {
  background: #2a2a2a;
  border-radius: 8px;
  width: 100%;
  max-width: 600px;
  padding: 40px;
  box-shadow: 0 4px 16px #0006;
}

h1 {
  color: #f5f5f5;
  margin-bottom: 8px;
  font-size: 1.75rem;
  font-weight: 500;
}

.subtitle {
  color: #999;
  margin-bottom: 32px;
  font-size: .95rem;
  font-weight: 300;
}

.options {
  flex-direction: column;
  gap: 12px;
  margin-bottom: 32px;
  display: flex;
}

.option {
  background: #333;
  border: 1px solid #404040;
  border-radius: 6px;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  transition: all .2s;
  display: flex;
}

.option:hover {
  background: #3a3a3a;
  border-color: #555;
}

.option-info h3 {
  color: #f5f5f5;
  margin-bottom: 4px;
  font-size: 1rem;
  font-weight: 500;
}

.option-info p {
  color: #999;
  font-size: .85rem;
  font-weight: 300;
}

.btn {
  color: #f5f5f5;
  white-space: nowrap;
  background: #4a4a4a;
  border-radius: 4px;
  padding: 8px 20px;
  font-size: .9rem;
  font-weight: 400;
  text-decoration: none;
  transition: background .2s;
}

.btn:hover {
  background: #5a5a5a;
}

.info {
  color: #888;
  border-top: 1px solid #404040;
  padding-top: 24px;
  font-size: .9rem;
  font-weight: 300;
  line-height: 1.6;
}

@media (width <= 600px) {
  .container {
    padding: 24px;
  }

  .option {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }

  .btn {
    text-align: center;
    width: 100%;
  }
}
