 * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
      }

      body {
        font-family: 'Ubuntu', sans-serif;
        background: #1a1a1a;
        min-height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px;
        color: #e0e0e0;
      }

      .container {
        max-width: 600px;
        width: 100%;
        background: #2a2a2a;
        padding: 40px;
        border-radius: 8px;
        box-shadow: 0 4px 16px rgba(0,0,0,0.4);
      }

      h1 {
        font-size: 1.75rem;
        font-weight: 500;
        margin-bottom: 8px;
        color: #f5f5f5;
      }

      .subtitle {
        color: #999;
        font-size: 0.95rem;
        font-weight: 300;
        margin-bottom: 32px;
      }

      .options {
        display: flex;
        flex-direction: column;
        gap: 12px;
        margin-bottom: 32px;
      }

      .option {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 16px 20px;
        border: 1px solid #404040;
        border-radius: 6px;
        background: #333;
        transition: all 0.2s;
      }

      .option:hover {
        border-color: #555;
        background: #3a3a3a;
      }

      .option-info h3 {
        font-size: 1rem;
        font-weight: 500;
        margin-bottom: 4px;
        color: #f5f5f5;
      }

      .option-info p {
        font-size: 0.85rem;
        font-weight: 300;
        color: #999;
      }

      .btn {
        padding: 8px 20px;
        background: #4a4a4a;
        color: #f5f5f5;
        text-decoration: none;
        border-radius: 4px;
        font-size: 0.9rem;
        font-weight: 400;
        transition: background 0.2s;
        white-space: nowrap;
      }

      .btn:hover {
        background: #5a5a5a;
      }

      .info {
        padding-top: 24px;
        border-top: 1px solid #404040;
        font-size: 0.9rem;
        font-weight: 300;
        color: #888;
        line-height: 1.6;
      }

      @media (max-width: 600px) {
        .container {
          padding: 24px;
        }

        .option {
          flex-direction: column;
          align-items: flex-start;
          gap: 12px;
        }

        .btn {
          width: 100%;
          text-align: center;
        }
      }